@import url('https://fonts.googleapis.com/css?family=Lato:300');
$large-screen: "only screen and (min-width: 1200px)";

.landing-page {
  background-color: #2489FF;
  height: 100vh;
  width: 100vw;

  color: white;
  font-family: 'Lato', sans-serif;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .title-container {
    display: flex;
    flex-direction: column;
    align-items: center;

    .logo {
      width: 80vw;
      @media #{$large-screen} {
        width: 800px;
      }
    }

    h2 {
      @media #{$large-screen} {
        font-size: 25px;
      }
      font-size: 4vw;
    }
  }

  .email-us-link {
    text-decoration: none;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3vw;

    @media #{$large-screen} {
      font-size: 20px;
    }

    .email-icon {
      height: 1em;
      margin-right: 10px;
    }
  }
}